import {Link} from 'react-router-dom'
import React from "react";



export const Navigation_map = ({category, id}) => (
  <Link key={id} to="/">
    <div key={id} onClick={(e) => (selectFilter('filter/' + e.target.innerText))} className="navigation-category">
      {category.name}
    </div>
  </Link>
);



export const Navigation = ({navigation, selectFilter}) => (
  <div className="navigation">
    {navigation.fetching ? undefined :
      navigation.categories.length === undefined ? undefined :
        <div className="exsistens">
          {navigation.categories.map((category, id) => (<Navigation_map key={id} category={category} id={id}/>))}
        </div>
    }
  </div>
);



export default Navigation;



