import * as type from "../constans/Shop_constants"
import axios from 'axios';
import { fetchApi } from '../../actions/actionCreators';





export function getItems(url) {
  return fetchApi(type.GET_ITEMS, url)
}


export function getCategories(url) {
  return fetchApi(type.GET_CATEGORIES, url)
}

export function selectFilter(url) {
  return fetchApi(type.FILTER_CATEGORIES, url)
}

export function getDetailArticle(url) {
  return fetchApi(type.GET_DETAIL, url)
}




