import * as style from '../../css/base.css'
import { Link } from 'react-router-dom';
import React from "react";



export const Items_Row = ({item}) => (
  <div key={item.id} className="item">
    <Link key={item.id} className={style.link_item} to={{pathname: item.id}}>
      <div className="head-item">
        <div className="img">
          <img src={item.pics} alt=""/>
        </div>
      </div>
      <div className="bottom-item">
        <div className="title-item">
          <div className="title">
            {item.title}
          </div>
        </div>
        <div className="price-item">
          <div className="nosale">
            <h2>{item.price} Руб</h2>
          </div>
        </div>
        <div className="buy">
          Купить
        </div>
      </div>
    </Link>
  </div>
);



export const Items = ({items}) => (
<div className="row">
  <div className={style.content}>
    {items.fetching ? undefined :
      items.items.length === undefined ?
      <div>Nothing Items...</div> :
          items.items.map((item) => (<Items_Row key={item.id} item={item}/>))
    }
  </div>
</div>
);

export default Items;



